import React from 'react'
import Star from "../../assests/icons/star.svg"
import "../../assests/css/personreview.css"
import { useSpring, animated } from 'react-spring'
const calc = (x, y) => [-(y - window.innerHeight / 2) / 20, (x - window.innerWidth / 2) / 20, 1.1]
const trans = (x, y, s) => `perspective(600px) rotateX(${x}deg) rotateY(${y}deg) scale(${s})`

function PersonReview({name,classN,dateS,text}) {
    const [props, set] = useSpring(() => ({ xys: [0, 0, 1], config: { mass: 5, tension: 350, friction: 40 } }))
    return (
    <animated.div
      className="card"
      onMouseMove={({ clientX: x, clientY: y }) => set({ xys: calc(x, y) })}
      onMouseLeave={() => set({ xys: [0, 0, 1] })}
      style={{ transform: props.xys.to(trans) }}
    >
        <div className="reviewCard">
            <div className={classN}></div>
            <div className= "LowerContainer">
                <div className="reviewName">{name}</div>
                <div className="reviewDate">{dateS}</div>
                <div className="reviewStars">
                    <img src={Star} alt="star rating"/>
                    <img src={Star}alt="star rating"/>
                    <img src={Star}alt="star rating"/>
                    <img src={Star}alt="star rating"/>
                    <img src={Star}alt="star rating"/>
                </div>
                <div className="reviewText">
                {text}
                </div>
            </div>
        </div>
        </animated.div>
    )
}

export default PersonReview
