import React from 'react'
import "../../assests/css/review.css"
import "react-multi-carousel/lib/styles.css";
import Carousel from "react-multi-carousel";
import PersonReview from './PersonReview';

const responsive = {
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 1,
      slidesToSlide: 1 // optional, default to 1.
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 1,
      slidesToSlide: 1 // optional, default to 1.
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
      slidesToSlide: 1 // optional, default to 1.
    }
  };
function Review() {
    return (
        <div className="hreview">
            <div className="hreviewinfo">What people think about us</div>
            <div>
            <Carousel
            swipeable={false}
            draggable={false}
            showDots={true}
            responsive={responsive}
            ssr={true} // means to render carousel on server-side.
            infinite={true}
            // autoPlay={this.props.deviceType !== "mobile" ? true : false}
            autoPlaySpeed={1000}
            keyBoardControl={true}
            customTransition="all .5"
            transitionDuration={500}
            containerClass="carousel-container"
            removeArrowOnDeviceType={["tablet"]}
            // deviceType={this.props.deviceType}
            dotListClass="custom-dot-list-style"
            itemClass="carousel-item-padding-40-px"
            >
            <div><PersonReview name = "Ishita Mehta" dateS="Stayed 18 Feb, 2021" classN="cardPhoto" text="It was very nice hotel with cleanliness. Staff behavior was good and polite. They welcome us very well. Issue was only that Lift was not in working and we were allotted to 3rd floor and amenities articles were in corner of gallery which were giving bad feeling. Breakfast was good and support of the staff was also very nice. Location is not good as per atmosphere, it is very nearby most of the popular places but self location in a narrow street is not good. Overall it was a good experience and could recommend. "/></div>
            <div><PersonReview name = "Tushar Chopra" dateS="Stayed 20 Jan, 2021" classN="cardPhoto1" text="Absolutely outstanding hotel perfectly located. Helpful, friendly staff who go above and beyond to make sure you have the best time. It was my birthday during our stay, which we had not mentioned, but they obviously saw that at the check in. It was a lovely suprise when they left a bottle of champagne in the room! Also an excellent breakfast buffet. We will definetely come back with friends and I would highly recommend this hotel"/></div>
            <div><PersonReview name = "Karan Veer Raghuvanshi" dateS="Stayed 18 Jan, 2021" classN="cardPhoto2" text="They were extremely accommodating and allowed us to check in early at like 10am. We got to hotel super early and I didn’t wanna wait. So this was a big plus. The sevice was exceptional as well. Would definitely send a friend there.The rooms were clean, very comfortable, and the staff was amazing. They went over and beyond to help make our stay enjoyable.The staff at this property are all great!. Overall, I had a great experience."/></div>
            <div><PersonReview name = "Aashna Juneja" dateS="Stayed 07 Jan, 2021" classN="cardPhoto3" text="Great value, clean, comfortable rooms, motel and site well maintained. Staff is polite, friendly and along with manager Jimmy, par excellence. They met and exceeded my expectations including enforcing strict Covid 19 restrictions with guests and staff. along with social distancing at all times. During this pandemic, I can honestly say I trust the people, the place and their practices to ensure my safety and comfort."/></div>
            </Carousel>
            </div>
        </div>
    )
}

export default Review
