import React from 'react'
import "../../assests/css/upperheader.css"
import Logo from "../../assests/imgs/Logo/complete.svg"
import Chakr from "../../assests/imgs/Logo/chakr.svg"
import TextLogo from "../../assests/imgs/Logo/text.svg"
function UpperHeader() {
    return (
        <header className="upperHeader">
            <a className="logoChakr" href="/#/"><img  alt="logo" src={Chakr}/></a>
            <a className="logoText" href="/#/"><img  alt="logo" src={TextLogo}/></a>
        </header>
    )
}

export default UpperHeader
